@import url(http://fonts.googleapis.com/css?family=Roboto:500,300);
* {
  box-sizing: border-box;
}

html {
  font-family: "Roboto", Helvetica, sans-serif;
}

body {
  margin: 0;
  background: #fff;
  color: #555f66;
}

h1, h2, h3, h4, h5, h6 {
  /* color: #666f77; */
  font-weight: 300;
  line-height: 1em;
  text-transform: uppercase;
  letter-spacing: 0.125em;
}

.slide-in-left-enter {
  transform: translateX(-100%);
}

.slide-in-left-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.variety-item {
  margin: 1rem;
  width: calc(45% - 2rem);
  min-width: 17.5rem;
  /* display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  padding: 1rem;
  color: #292929;
  background: white; */
}

.variety-item :hover,
.variety-item :active {
  background: #b8cfd5;
}

.variety-item__content {
  padding: 0;
}

.variety-item__info h2 {
  font-size: 1.5rem;
  margin: 0 0 0.5rem 0;
  font-weight: normal;
  color: #b8cfd5;
}

.variety-item:hover h2,
.variety-item:active h2,
.variety-item:hover h3,
.variety-item:active h3 {
  color: #292929;
}

.variety-item__info h3 {
  margin: 0;
}
.card {
  position: relative;
  margin: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  padding: 1rem;
  overflow: hidden;
}

.varieties-list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  width: 90%;
  max-width: 50rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
